

@keyframes red-highlight {
  from {background-color: white;color:red}
  to {background-color: red;color:white}
  
} 

@keyframes green-highlight {
  from {background-color: white;color:green}
  to {background-color: green;color:white} 
}  

.dchange{
  padding:10px;border-radius: 5px;text-align: center;
}

[data-change]      { 
  
  animation-name: green-highlight ;
  animation-duration: 4s; background-color: green;color:white}
[data-change="0"]  {    }
[data-change^="-"] { animation-name: red-highlight ;
  animation-duration: 4s;  background-color: red;color:white }

