
.portfolio-item-editor{
    display: flex;
    flex-direction: column;
  }
  
  .service-monitor-card{  
    min-width:327px;
    max-width:327px;
    display: flex;
    flex-direction: column;
    width: 33.33%;
    padding:10px 20px;
    margin:10px;
    -moz-box-shadow:    inset 0 0 10px #CCC;
    -webkit-box-shadow: inset 0 0 10px #CCC;
    box-shadow:         inset 0 0 10px #CCC;
    flex: 0 1 calc(25% - 1em);
    background-color: #FFF;
    border-radius: 10px;
    min-height: 290px; 
    border: #d2d2eb 1px solid;
  }
  .service-monitor-card h3{
    color:#03045e; 
    text-transform: uppercase;
  }
  .service-monitor-card p {
    color:#03045e;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    letter-spacing: 1px;
  }
  .service-monitor-card span{
    color:#14213d
  }
  .icon{
    position:relative;
  }
  .video__icon {
  position: absolute;
  width: 50px;
  left: 20px;
  top: 20px; }
  .video__icon .circle--outer {border: 1px solid #e50040;}
  .video__icon .circle--inner {background: #e50040;}
  .video__icon .circle--inner:after {border: 2px solid #e50040;}
  .video__icon.active .circle--outer {border: 1px solid #308d11;}
  .video__icon.active .circle--inner {background: #308d11;}
  .video__icon.active .circle--inner:after {border: 2px solid #308d11;}
  .video__icon.listen .circle--outer {border: 1px solid #f0d465;}
  .video__icon.listen .circle--inner {background: #f0d465;}
  .video__icon.listen .circle--inner:after {border: 2px solid #f0d465;}
  
  .video__icon .circle--outer { 
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 0 auto 5px;
    position: relative;
    opacity: .8;
    -webkit-animation: circle 2s ease-in-out infinite;
            animation: circle 2s ease-in-out infinite; }
  .video__icon .circle--inner { 
    left: 15px;
    top: 10px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    position: absolute;
    opacity: .8; }
    .video__icon .circle--inner:after {
      content: '';
      display: block; 
      border-radius: 50%;
      width: 28px;
      height: 28px;
      top: -4px;
      left: -4px;
      position: absolute;
      opacity: .8;
      -webkit-animation: circle 2s ease-in-out .2s infinite;
              animation: circle 2s ease-in-out .2s infinite; }
  .video__icon p {
    color: #000;
    text-align: center; }

    .video__icon .circle--outer:before, .video__icon .circle--outer:after,
.video__icon .circle--inner:before, .video__icon .circle--inner:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.fa-caret-up{
    color:green;
}
.fa-caret-down{
